#content-link {
  text-decoration: none;
  color: black;
}
#content-link:hover {
  text-decoration: none !important;
  color: green;
}

.boutique-container {
  padding: 30px;
  max-width: 100%;
  display: flex;
  justify-content: center;
}
.boutique-container h2 {
  text-align: center;
}
.boutique-container hr {
  margin: 40px;
  border-color: black;
}
.boutique-container svg {
  color: green;
  font-size: 25px;
  margin: 0 20px;
}
.boutique-container svg:hover {
  color: white;
  background-color: green;
  font-size: 25px;
  margin: 0 20px;
  border-radius: 50px;
}
#product-button {
  background-color: green;
  border: solid transparent;
}
#product-button:hover {
  background-color: green;
  border: solid transparent;
}
