.showCart {
  width: 100%;
  padding: 20px;
  align-items: center;
  justify-content: center;
}
.showCart img {
  width: 70px;
  height: 70px;
}
#cart-icon {
  color: green;
  position: relative;
  bottom: -16px;
  font-size: 25px;
}
.vcenter {
  padding: 50px 20px;
  display: flex;
}
.center-table {
  margin: auto;
  width: 60% !important;
}
.showCart td p {
  transform: translateY(80%);
  text-align: center;
}
.showCart td {
  text-align: center;
}
.column-left {
  width: 100%;
}
.column-right {
  width: 30%;
  border: solid 2px green;
  border-radius: 10px;
  padding: 10px;
}
.column-flex {
  display: flex;
}
.column-split {
  width: 50%;
}
.column-split p {
  padding: 10px;
}
.column-button {
  text-align: center;
  margin: 20px auto;
}
