.moncompte-body .row {
  margin: auto;
}

.moncompte-body h3 {
  padding: 20px;
  color: white;
  font-size: 22px;
}
.moncompte-contact {
  width: 500px;
  margin: auto;
}
.moncompte-infos {
  text-align: center;
}
.moncompte-infos .row {
  margin: auto;
}
.moncompte-card {
  width: 500px;
  margin: 50px auto;
}
.tabSelect {
  padding: 20px;
}
.breadcrumb {
  padding: 0.75rem 1rem;
  margin-bottom: 0;
  list-style: none;
  background-color: green !important;
  border-radius: 0.25rem;
  color: white;
}
.breadcrumb p {
  margin: 0;
}
.background-col.col {
  background-color: #343a40;
  max-width: 20%;
  min-height: 90vh;
}
.monCompte-nav {
  padding-top: 50px;
}
.title-moncompte h2 {
  color: green;
  margin: 50px auto;
  text-align: center;
}
.display-edit {
  text-align: left;
  border-radius: 10px;
}
.display-edit-right {
  text-align: left;
  border-radius: 10px;
  margin: auto;
}
.moncompte-link {
  text-align: right;
  margin: auto;
  text-decoration: none;
}
.moncompte-link:hover {
  text-decoration: none;
}
.display-edit-right input {
  margin-bottom: 15px;
  border: 0;
  outline-style: none;
  border-bottom: solid 1px black;
  width: 400px;
}
.display-edit-right label {
  margin: 0;
}
.form-contact {
  text-align: center;
}
.contact-form input {
  width: 400px;
  margin: 10px;
  border: 0;
  padding: 5px;
  border-bottom: solid 1px black;
  outline-style: none;
}
.contact-form textarea {
  width: 400px;
  border-radius: 4px;
  margin: 10px;
  border: 0;
  padding: 5px;
  height: 140px;
  border: solid 1px black;
  outline-style: none;
}
