.accueil-background img {
  width: 100%;
  height: 90vh;
}
.home-container h1 {
  text-align: center;
  margin: 50px 0;
}
.home-desc {
  display: flex;
  width: 70%;
  margin: 20px auto 70px auto;
}
.home-desc img {
  width: 500px;
  height: auto;
}
.home-desc p {
  text-align: left;
  padding: 40px;
}
.home-galery {
  padding-bottom: 40px;
  background-image: url("/bg.jpg");
}
.home-galery h1 {
  color: white;
  padding: 30px 0;
  margin: 0;
}
.galery img {
  width: 300px;
  height: 200px;
}
.galery {
  justify-content: center;
  margin: 0px !important;
  padding-top: 15px;
  padding-bottom: 15px;
}
.galery .col {
  flex-grow: unset;
}
.home-location img {
  width: 400px;
}
.home-location p {
  margin: 0 50px;
}
.home-location .row {
  margin: 0;
  justify-content: center;
}
.home-location .col {
  flex-grow: unset;
}
#address {
  padding: 20px 20px 50px 20px;
}
#address-container {
  padding-bottom: 50px;
}
#address-container .col {
  margin: 30px;
}
.home-form {
  padding: 20px;
}
.home-form input {
  width: 400px;
  margin: 10px;
  border: 0;
  padding: 5px;
  background-color: rgb(241, 237, 237);
  border-bottom: solid 1px black;
  outline-style: none;
}
.home-form textarea {
  width: 400px;
  border-radius: 4px;
  margin: 10px;
  border: 0;
  padding: 5px;
  height: 140px;
  border: solid 1px black;
  background-color: rgb(241, 237, 237);
  outline-style: none;
}
.btn-primary {
  background-color: green !important;
  border: solid transparent !important;
}
.btn-primary:hover {
  background-color: green !important;
  border: solid transparent !important;
  filter: brightness(115%);
}
.form-button {
  text-align: center;
  margin: 20px;
}
