#logo {
  margin: 20px;
  width: auto;
  height: 70px;
}
.header {
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  box-shadow: 4px 2px 8px rgb(0, 0, 0);
  height: 110px;
}

.nav-link {
  margin: 0px 5px;
}
.navbar-dark .navbar-nav .nav-link {
  color: black !important;
  font-size: 20px;
  outline-style: none;
}
.navbar-dark .navbar-nav .nav-link:hover {
  border-bottom: solid 3px green;
}
.form-inline .form-control {
  width: 500px !important;
  text-align: center;
  margin: 10px;
  border: solid black 2px;
  border-radius: 0px;
}
.form-inline {
  justify-content: center;
  padding: 10px 0px;
  background-color: transparent;
  border: solid 1px transparent;
  border-radius: 0px;
}
#Menu {
  float: right;
  top: 20px;
}
.navbar-nav svg {
  font-size: 20px;
  vertical-align: sub;
  position: relative;
  bottom: 4px;
  margin-right: 10px;
}
.menu-mobile {
  display: none;
}
.menu-mobile-global {
  display: none;
}
#search-toogle {
  background-color: #303030ef;
}

#btn {
  color: white;
  border: transparent solid 1px;
  background-color: transparent;
}
#btn:hover {
  border-bottom: solid 1px green !important;
}
#btn:focus {
  box-shadow: none !important;
}
.modal-body a {
  text-decoration: none;
  color: black;
}
.modal-body a:hover {
  text-decoration: none;
  color: green;
}
.form-control:focus {
  box-shadow: none !important;
  border-color: green !important;
}
#toogleCategories {
  color: black !important;
  bottom: -5px;
  background-color: transparent !important;
  padding-left: 0;
}
.btn-primary.focus,
.btn-primary:focus {
  box-shadow: 0 0 0 0.2rem transparent !important;
}
.dropdown-item:hover {
  background-color: green !important;
  color: white !important;
}
.dropdown-item.active,
.dropdown-item:active {
  background-color: green !important;
  color: white !important;
}
.menu-mobile-global li {
  list-style: none;
  padding: 10px 0;
}
.menu-mobile-global a {
  color: black;
}
.menu-mobile-global a:hover {
  color: green;
  text-decoration: none;
}
#login-modal {
  margin: 0 !important;
  display: flex;
  justify-content: center;
  max-width: 626px;
  position: relative;
  margin: 100px auto !important;
  height: 417px !important;
}
#body-modal {
  background-image: url("/fond-modal.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: auto;
  border-radius: 3px;
}
@media screen and (max-width: 916px) {
  .navbar-dark .navbar-nav .nav-link {
    color: white !important;
    font-size: 20px;
    display: none !important;
  }

  #toogleCategories {
    display: none;
  }
  .menu-mobile {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    font-size: 40px;
    color: green;
    margin-top: 35px;
    margin-right: 40px;
  }
  .nav {
    flex-direction: column;
    position: absolute;
    top: 20px;
    right: 100px;
    background-color: rgb(241, 237, 237);
    border: solid 3px green;
    border-radius: 10px;
    width: 300px;
  }
  .menu-mobile-global .nav-link {
    text-decoration: none;
    color: black;
    padding: 10px 20px;
    font-size: 20px;
  }
  .menu-mobile-global .nav-link:hover {
    text-decoration: none;
    color: green;
  }
  .menu-mobile-global {
    display: block;
  }
  #logo {
    margin: 20px;
    width: auto;
    height: 70px;
  }
  .hide-icon {
    display: none;
  }
  #connection {
    display: block;
  }
  #myAccount {
    display: block;
  }
}
